body {
	margin: 0;
	padding: 0;
	background-color: #F0EFE9;
	font-family: sans-serif;
}

button:hover {
	cursor: pointer;
}

#app {
	position: relative;
	width: 20%;
	margin: 50px auto;
	padding: 40px 35px 80px 45px;
	background-color: #FFFFFF;
}

.time {
	display: flex;
	justify-content: space-between;
	align-items: center;
	color: #616473;
	margin-bottom: 30px;
}

.time-week {
	text-transform: uppercase;
	font-weight: 500;
	font-size: 16px;
}

.date {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.date-day {
	margin-right: 10px;
	font-size: 45px;
	font-weight: 400;
}

.date-info {
	display: flex;
	flex-direction: column;
	font-size: 16px;
}

.date-info__month {
	font-weight: 500;
	text-transform: uppercase;
}

.date-info__year {
	font-weight: 400;
}

.todo-list-item {
	display: flex;
	justify-content: space-between;
	align-items: center;
	font-weight: 500;
	font-size: 18px;
	color: #616473;
	margin-bottom: 35px;
}

.todo-list-item:last-of-type {
	margin-bottom: 0;
}

.todo-list-item__text {
	max-width: 70%;
}

.todo-list-item__buttons {
	display: flex;
	flex-direction: row;
}

.todo-list-item__button {
	width: 31px;
	height: 31px;
	border-radius: 50%;
	outline: none;
	background-color: #FFFFFF;
}

.done {
	color: #BDC0CA;
}

.done-btn, .delete-btn {
	display: flex;
	justify-content: center;
	align-items: center;
}

.done-btn:hover, .delete-btn:hover {
	transition: color 0.3s, border 0.3s;
}

.done-btn {
	color: #21D387;
	border: 4px solid #21D387;
	margin-right: 5px;
}

.done-btn:hover {
	color: #1AA86C;
	border: 4px solid #1AA86C;
}

.delete-btn {
	color: red;
	border: 4px solid red;
}

.delete-btn:hover {
	color: #CC0000;
	border: 4px solid #CC0000;
}

.wait-btn {
	color: #3E434F;
	border: 4px solid #EEF0F5;
}

#add-item {
	position: absolute;
	bottom: -45px;
	left: 50%;
	transform: translateX(-50%);
	padding: 15px 30px;
	border: none;
	background-color: #50E3A4;
	border-radius: 50%;
	color: #46BE8B;
	font-family: sans-serif;
	font-size: 50px;
	font-weight: 400;
	text-transform: uppercase;
	outline: none;
	transition: box-shadow 0.3s;
}

#add-item:hover {
	box-shadow: 0 3px 10px rgba(62, 192, 136, 0.8);
}

.new-item-form {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 20px;
}

.new-item-form__input {
	width: 85%;
	padding: 7px;
	box-sizing: border-box;
	font-weight: 500;
	font-size: 17px;
	color: #616473;
	outline: none;
	border: 1px solid #616473;
	border-radius: 5px;
}

.new-item-form .done-btn {
	margin-right: 0;
}

@media (max-width: 1200px) and (min-width: 993px) {
	#app {
		width: 40%;
	}
}

@media (max-width: 992px) and (min-width: 769px) {
	body {
		background-color: #FFFFFF;
	}

	#main-wrap {
		display: flex;
	}

	#app {
		width: 100%;
		margin: 0;
		padding: 15px;
	}

	.time {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		margin: 0;
		background-color: #FFFFFF;
		padding: 15px;
	}

	.todo-list {
		overflow-y: scroll;
		margin: 80px 0 65px 0;
	}
	
	.todo-list-item {
		margin-bottom: 15px;
	}

	.done-btn:hover, .delete-btn:hover, #add-item:hover {
		transition-property: none;
	}

	#add-item {
		position: fixed;
		bottom: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		font-size: 45px;
		padding: 5px;
		border-radius: 0;
	}

	#add-item:hover {
		box-shadow: none;
	}

	.new-item-form__input {
		width: 86%;
	}
}

